.centered {
  max-width: 1200px;
  margin: 0 auto;
}

.clearfix::after, .clearfix::before {
  content: "";
  display: block;
  clear: both;
}

.dimmer {
  width: 100vw;
  height: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
}

[class$=__is-content] {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}
@media screen and (max-width: 810px) {
  [class$=__is-content] {
    padding: 0 25px;
  }
}

*, html, body {
  box-sizing: border-box;
}

html, body {
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 1.15;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjusted: 100%;
  -ms-text-size-adjusted: 100%;
}

body {
  margin: 0;
  overflow-x: hidden;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

img {
  border-style: none;
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  -ms-interpolation-mode: bicubic;
  width: 100%;
}

button, input, optgroup, select, textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button::-moz-focusring,
[type=button]::-moz-focusring,
[type=reset]::-moz-focusring,
[type=submit]::-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

textarea {
  overflow: auto;
}

b, strong {
  font-weight: bolder;
}

pre, code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
  max-width: 100vw;
  overflow: scroll;
}

small {
  font-size: 80%;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-transform: capitalize;
}

h1, .h1 {
  font-size: clamp(2rem, 4.4175vw + 0.3434375rem, 4.20875rem);
  line-height: 67.34px;
  font-weight: 300;
  letter-spacing: -0.015625em;
}

h2, .h2 {
  font-size: clamp(1.5rem, 3.314625vw + 0.257015625rem, 3.1573125rem);
  line-height: 50.517px;
  font-weight: 300;
  letter-spacing: -0.015625em;
}

h3, .h3 {
  font-size: clamp(1.17rem, 2.397125vw + 0.271078125rem, 2.3685625rem);
  font-weight: 300;
  letter-spacing: -0.015625em;
}

h4, .h4 {
  font-size: clamp(1rem, 1.55375vw + 0.41734375rem, 1.776875rem);
  font-weight: 400;
  letter-spacing: normal;
}

h5, .h5 {
  font-size: clamp(0.83rem, 1.006vw + 0.45275rem, 1.333rem);
  line-height: 28px;
  font-weight: 400;
  letter-spacing: normal;
}

h6, .h6 {
  font-size: clamp(0.67rem, 1.16vw + 0.235rem, 1.25rem);
  line-height: 32px;
  font-weight: 500;
  letter-spacing: 0.0125em;
}

